body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-container {
	margin: auto;
	background-color: #2962ff;
	width: 90%;
	padding: 1rem 1rem 1rem 1rem;
	box-shadow: 0 5px 5px darkgray;
	/* border: 1px solid black; */
}

.empty-div{
	width: 85%;
}

.ul {
	padding: 0;
	list-style-type: none;
	display: flex;
	justify-content: space-evenly;
}

.navbar-li {
	padding: 0.3rem;
}

.li-container {
	display: flex;
	padding: 0 1rem 0 0;
	color: white;
}

.nav-link {
	text-decoration: none;
	font-family: "Roboto", sans-serif;
	font-weight: 200;
	font-size: 1.2rem;
	color: white;
}

@media all and (max-width: 375px) {
	.navbar-container {
		margin: auto;
		display: block;
		width: 90%;
		/* border: 1px solid red; */
	}
	.li-container {
		display: flex;
		padding: 0 0.1rem 0 0;
		color: white;
	}
	
}

.error-container {
	margin: auto;
	width: 65%;
	border: none;
	/* border: 1px solid black; */
}

.error-details {
    display: flex;
    align-items: center;
    justify-content: center;
	margin: 0.8rem 0 0 0;
	border: 1px solid red;
	background-color: #ffd6d6;
	border-radius: 8px 8px 8px 8px;
}

.error-icon {
    vertical-align: middle;
}

.error-message {
	text-align: center;
	color: #ff0000;
    font-family: "Quicksand", sans-serif;
    margin-left: 0.3rem;
	/* vertical-align: text-bottom; */
}

@media all and (max-width: 375px) {
	.error-container {
		margin: auto;
		width: 100%;
		/* border: 1px solid black; */
	}
	.error-message {
		text-align: center;
		color: #ff0000;
		font-family: "Quicksand", sans-serif;
		margin-left: 0.3rem;
		font-size: 13px;
		/* vertical-align: text-bottom; */
	}
}

.register-container {
	margin: auto;
	/* border: 1px solid green; */
	width: 35%;
	/* border: 1px solid grey; */
	padding: 2rem 0 0 0;
}

.register-card {
	margin: 1rem 0 0 0;
	border: 1px solid grey;
	border-radius: 8px 8px 8px 8px;
	box-shadow: 5px 3px 8px #999999;
}

.register-title {
	margin: auto;
	text-align: center;
	padding: 2rem 0 2rem 0;
	font-family: "Pacifico", cursive;
	font-weight: 700;
	font-size: 3rem;
	/* border: 1px solid red; */
}

.register-form {
	margin: auto;
	width: 90%;
	/* border: 1px solid purple; */
	padding: 2rem 2rem 2rem 2rem;
}

.register-field {
	/* border: 1px solid blue; */
	margin: auto;
	padding: 0.5rem 1.5rem 0.5rem 1.5rem;
	margin: 0.3rem 0 0 0;
}
.register-input {
	box-sizing: border-box;
	width: 100%;
	padding: 0.6rem;
}

.register-submit {
	box-sizing: border-box;
	width: 100%;
	padding: 2rem 55% 0 45%;
}

.btn-submit {
	padding: 0.5rem 0.5rem 0.5rem 0.5rem;
	background-color: green;
	color: white;
	border: 1px solid green;
	border-radius: 5px;
	font-size: 1rem;
}

/* Media Query */

@media all and (max-width: 375px) {
	.register-container {
		margin: auto;
		/* border: 1px solid green; */
		width: 90%;
		/* border: 1px solid grey; */
	}

	.register-form {
		margin: auto;
		width: 100%;
		/* border: 1px solid purple; */
		padding: 0 0 0 0;
	}

	.register-submit {
		box-sizing: border-box;
		width: 100%;
		padding: 1rem 58% 1rem 42%;
	}

	.register-title {
		margin: auto;
		text-align: center;
		padding: 2rem 0 2rem 0;
		font-family: "Pacifico", cursive;
		font-weight: 700;
		font-size: 2rem;
		/* border: 1px solid red; */
	}
}

