.postcard-container {
	border: 1px solid grey;
	border-radius: 8px 8px 8px 8px;
	box-shadow: 1px 1px 2px #bdbdbd;
	background: linear-gradient(to bottom, white, #f3f3f3);
}

.postcard-datetime {
	margin: 0;
	padding: 0 0 0 1rem;
	font-family: "Roboto", sans-serif;
	font-size: 11px;
	color: #5d5e5e;
}

.postcard-heading {
	margin: 0;
	padding: 0.8rem 0 0 1rem;
	font-family: "Roboto", sans-serif;
	font-size: 28px;
}

.postcard-link {
	text-decoration: none;
	color: #282828;
}

.postcard-post-container {
	margin-top: 0;
	padding: 0.8rem 1rem 0 1rem;
}
.postcard-text {
	margin-top: 0.3rem;
	word-wrap: break-word;
	font-family: "Roboto", sans-serif;
}
.postcard-deleteicon-container {
	padding: 0 0 0.5rem 1rem;
	/* border: 1px solid black; */
}

.postcard-deleteicon:hover {
	cursor: pointer;
}

.userposts-container {
	width: 80%;
	margin: auto;
	padding: 2rem 0 0 0;
}
.userpostsheading-heading {
	display: flex;
	align-items: center;
	font-family: "Raleway", sans-serif;
}

.userposts-btn-add {
	margin-left: 5%;
	color: white;
	background-color: #10ab00;
	border: none;
	height: 35px;
	padding: 0.5rem 0.5rem 0.5rem 0.5rem;
	border-radius: 8px 8px 8px 8px;
	font-size: 15px;
	font-family: "Raleway", sans-serif;
	outline: none;
	
}

.userposts-btn-add:hover {
	color: black;
	cursor: pointer;
}

.userposts-cardlist {
	display: grid;
	grid-template-columns: 450px 450px 450px;
	grid-auto-rows: -webkit-max-content;
	grid-auto-rows: max-content;
	grid-gap: 15px 10px;
	gap: 15px 10px;
}

@media all and (max-width: 375px) {
	.userposts-container {
		width: 100%;
	}

	.userpostsheading-heading {
		display: flex;
		align-items: center;
		margin-left: 1rem;
		font-family: "Raleway", sans-serif;
	}

	.userposts-btn-add {
		margin-left: 38%;
		color: white;
		background-color: #10ab00;
		border: none;
		height: 45px;
		padding: 0.5rem 0.5rem 0.5rem 0.5rem;
		border-radius: 8px 8px 8px 8px;
		font-size: 15px;
		font-family: "Raleway", sans-serif;
	}

	.userposts-cardlist {
		padding: 0 0.2rem 0 0.2rem;
		display: grid;
		grid-template-columns: 368px;
		grid-auto-rows: -webkit-max-content;
		grid-auto-rows: max-content;
		grid-gap: 15px 10px;
		gap: 15px 10px;
	}
}

.modal-newpost-title {
	margin: 0;
	margin-bottom: 2rem;
	text-align: center;
	background: linear-gradient(to right, #0084d5, #0f46bb);
	padding: 5px;
	color: white;
	font-family: "Pacifico", cursive;
	font-weight: 100;
	font-size: 30px;
}

.modal-newpost-form-container {
	width: 90%;
	margin: auto;
	padding: 0 0 0 7%;
}

.modal-newpost-buttons {
	display: flex;
	justify-content: flex-end;
	margin-right: 2.4rem;
}

.modal-newpost-button-add {
	padding: 8px;
	margin-right: 0.3rem;
	border: none;
	background-color: #23a815;
	border-radius: 8px;
	color: #f6f6f6;
	font-size: 16px;
	outline: none;
}

.modal-newpost-button-cancel {
	border: none;
	background-color: #ff3131;
	padding: 8px;
	border-radius: 8px;
	color: #f6f6f6;
	font-size: 16px;
	outline: none;
}

.modal-newpost-button-cancel:hover {
	color: black;
	cursor: pointer;
}

.modal-newpost-button-add:hover {
	color: black;
	cursor: pointer;
}

.modal-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(255, 255, 255, 0.75);
}

.modal-content {
	position: absolute;
	top: 30%;
	left: 30%;
	right: 40%;
	bottom: 40%;
	border: 1px solid #0f46bb;
	background: #fff;
	overflow: auto;
	-webkit-overflow-scrolling: touch;
	border-radius: 15px;
	outline: none;
	padding: 0;
}

@media all and (max-width: 375px) {
	.modal-content {
		position: absolute;
		top: 50%;
		left: 5%;
		right: 5%;
		bottom: 10%;
		border: 1px solid #0f46bb;
		background: #fff;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
		border-radius: 15px;
		outline: none;
		padding: 0;
	}

	.modal-newpost-form-container {
		width: auto;
		margin: auto;
		padding: 0 0 0 0;
	}

	.modal-newpost-buttons {
		display: flex;
		justify-content: center;
		margin-right: 0;
		margin-top: 1%;
	}

	.modal-newpost-textarea {
		margin-left: 7%;
		width: 85%;
	}
}

